import React from 'react'
import { useTable, useSortBy, useGlobalFilter,useBlockLayout,usePagination} from 'react-table'
import Papa from "papaparse";
import { useExportData } from "react-table-plugins";
import { GlobalFilter } from './global_filter';

export const BasicReactTable = ({row_data,column_data,footer,global_search,csv_file,pagination,default_page_size}) => {

   const data = React.useMemo(
     () => row_data,
   )

   const columns = React.useMemo(
     () => column_data,
   )

   const {
     getTableProps,
     getTableBodyProps,
     headerGroups,
     prepareRow,
     exportData,
     state,       
     nextPage,
     previousPage,
     canNextPage,
     canPreviousPage, 
     page,  
     setPageSize,
     pageOptions,
     setGlobalFilter,
     gotoPage,    
     rows,
     footerGroups, 
   } = useTable({ columns, data,  initialState : {pageSize: default_page_size},getExportFileBlob },useGlobalFilter,useSortBy,useBlockLayout,usePagination,useExportData)   

   function getExportFileBlob({ columns, data, fileType, fileName }) {       
        if (fileType === "csv") {            
            const headerNames = columns.map((col) => col.exportValue);
            const csvString = Papa.unparse({ fields: headerNames, data });
            return new Blob([csvString], { type: "text/csv" });
        }  
        return false;        
   }

   const {globalFilter} = state 
   const {pageIndex, pageSize} = state  

   return (
    <> 
    {csv_file &&
        <button className='btn btn-info btn-sm rect_tablev7_export_button'
            onClick={() => {exportData("csv", true)}}
        >Export to CSV</button>
    }
        {global_search &&
        <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter}/>
        }
        <div className={'react_table_v7 table-responsive customer_list_reactTable border_bot'}>        
            <table {...getTableProps()}>
                <thead >
                    {headerGroups.map(headerGroup => (
                    <tr  className='no-margin no-padding' style={{border:"1px sold red",borderWidth:'100%'}} {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map(column => (
                        <th
                            {...column.getHeaderProps(column.getSortByToggleProps())}                       
                        >
                            {column.render('Header')}
                            <span>
                                {column.isSorted ? (column.isSortedDesc ? " ⇑" : " ⇓") : ""}
                            </span>
                        </th>
                        ))}
                    </tr>
                    ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                    {page.map(row => {
                    prepareRow(row)
                    return (
                        <tr {...row.getRowProps()}>
                        {row.cells.map(cell => {
                            return (
                            <td
                                {...cell.getCellProps()}                            
                            >
                                {cell.render('Cell')}
                            </td>
                            )
                        })}
                        </tr>
                    )
                    })}
                </tbody>{footer &&
                <tfoot style={{color:'white'}}>
                {footerGroups.map(group => (
                <tr {...group.getFooterGroupProps()}>
                    {group.headers.map(column => (
                    <td {...column.getFooterProps()}>{column.render('Footer')}</td>
                    ))}
                </tr>
                ))}
            </tfoot>}
            </table>
        </div>
        {pagination&&

        <div className='d-flex justify-content-center align-items-center'  style={{marginTop:'10px'}}>
            <button className='btn btn-info gds_table_pagination' onClick={()=>previousPage()} disabled={!canPreviousPage}>Previous</button>
            <span style={{fontSize:'14px',color:'white',margin:'0px 30px'}}>
                Page<strong><span >
                <input className='btn btn-info no-margin'  style={{padding:'10px 5px 10px 9px',width:'40px'}} type="number" defaultValue={pageIndex+1} onChange={(e)=>{const pageNumber = e.target.value ? Number(e.target.value) -1 : 0
                    gotoPage(pageNumber)
                }} ></input>
            </span> of {pageOptions.length}</strong>
            </span>
           
            <select className='btn btn-info' value={pageSize} onChange={e=>setPageSize(Number(e.target.value))} style={{margin:'0px 30px',padding:'8px'}}>
                {
                    [5,20,50,100].map(pageSize => (
                        <option key={pageSize} value={pageSize}>Show{pageSize}</option>
                    ))
                }
            </select>
            <button className='btn btn-info gds_table_pagination' onClick={()=>nextPage()} disabled={!canNextPage}>Next</button>
        </div>
        }
     </>
   )
 }