    selectAccPrvdr = (event) => {
        if(this.roles === "super_admin" || this.roles === "it_admin"){
            this.toChange = "acc_prvdr";
            history.push('/management-dashboard')
        }else{
            this.toChange = "acc_prvdr_only";
        }
        console.log('"first"', "first")

        this.setState({toSelectMarket:true});                                  
    }

    selectMarket = (event) => {

        if(get('role_codes') ==="super_admin" || get('role_codes') ==="it_admin"){
            this.toChange = "market";
            this.setState({toSelectMarket:true});
        }
    }
componentDidMount(){

  if(get('role_codes') == 'investor' && window.location.pathname == "/" ){
    history.push('/management-dashboard')
  }

  var account = {lender_code : this.lender_code, "country_code": this.country_code,  to_recon : true, 'status' : 'enabled'};
  getRefAccounts(this.req({account})).then((response) => {

    if(!response){return };
    if(response.status === "success"){
        if(response.data != "")
        {   
      
            this.setState({server_time:response.server_time})
            const account_list = response.data.map((account,i)=>
            
            <li><Link to={`/account/${account.id}/statement` } target = "blank">{account.name}</Link>
            <p className='d-flex align-items-center no-padding no-margin  justify-content-center'>
            <p className='no-padding no-margin ' style={{color:'#dce0dd'}}>{this.timeDiff(response.data[i].float_acc_stmt_end_time)}</p>
            <p className='no-padding no-margin'>{this.status_handler(response.data[i].status)}</p>
            </p>
            </li>
            
            );
            
            this.setState({account_list}) 
        }     
        
    }else{
    
        alert(response.message + "\n" + JSON.stringify(response.data));  
    }
    
});


}

