import React, {Component} from 'react';

class Toggle extends Component {
    constructor(props) {
        super(props);
        this.state = {
            toggle: false
        };


        this.toggleState = this.toggleState.bind(this);

    }

    toggleState() {
        this.setState({
            toggle: !this.state.toggle
        },() => this.props.set_tog_val(this.state.toggle));
    }

    render() {
        return (
            <form className="switch-field">
                <p  className="d-flex justify-content-center" style={{color: 'white', fontSize : this.props.fontSize+"px"}} >{this.props.icon}{this.props.title}</p> 
                <input
                    type="radio"
                    id="switch_left"
                    name="switchToggle"
                    value={this.props.leftLabel}
                    onClick={this.toggleState}
                    checked={!this.props.val}
                />
                <label htmlFor="switch_left" style={{width : this.props.width+"px"}}>{this.props.leftLabel}</label>

                <input
                    type="radio"
                    id="switch_right"
                    name="switchToggle"
                    value={this.props.rightLabel}
                    onClick={this.toggleState}
                    checked={this.props.val}
                />
                <label htmlFor="switch_right" style={{width : this.props.width+"px"}}>{this.props.rightLabel}</label>
            </form>
        );
    }
}
export default Toggle;