import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {format_date, title_case, lbl_amt, rt_algn} from '../../helpers/common_helper';
import DisplayImage from '../common/component/DisplayImage';
import '../../styles/flow.css';
import {Link} from "react-router-dom";
import { SiGooglemaps } from 'react-icons/si';
import {Button,Modal} from 'react-bootstrap';
import {dd_value} from "../../actions/common_actions";
import CommonReactTable from '../../helpers/commonReact_table';
import { listFAUpradeRequests, updgradeReqStatus } from '../../actions/loan_actions';
import {get} from '../../helpers/storage_helper';

import LastNFAs from '../loan_appl/component/LastNFAs';
import Accordion from 'react-bootstrap/Accordion';
import {Card} from 'react-bootstrap'
import {BsChevronDown} from 'react-icons/bs';


class PendingFAUpgradeRequests extends FlowComponent {

	state = {
        rowdatas :[],
        copy : [],
        columns : [],
        approval_modal :false,
        modal_data:{},
        upgrade_amt:null,
        show_table: false
    }

	handleToggle = (e) => {

        switch(e){
            case("Account"):
                this.setState(state => state.show_table = !this.state.show_table);
                break;
        }
    }

    componentWillMount() {
        this.drawTable()
    }

    drawTable(){
        listFAUpradeRequests(this.req())
        .then((response) => {
            this.resp(response)
            if(!response){return}
            if(response){
                this.setState({refresh: false});
                this.setState(state => state.rowdatas = response.data)
                this.setState(state => state.copy = response.data)
                this.setState(state => state.columns =  this.columndatas())
            }
        });
    }

    columndatas(){
        return[
            {
                Header : "Cust ID",
                id: 'cust_id',
                minWidth:100,
                accessor: row => <Link style={{fontSize: "18px", color: "#17a2b8 !important"}} to={"/borrower/indiv/view/"+row.cust_id} target="_blank">{row.cust_id}</Link>
            },
            {
                Header : "Biz Name",
                id: 'biz_name',
                minWidth:100,
                accessor: row => {return title_case(row.biz_name)}
            },
            {
                Header : "Requested Amount",
                id: 'requested_amount',
                minWidth:100,
                accessor: row => {return lbl_amt(row.requested_amount , this.currency_code)}            
            },
            {
                Header : "Available amounts",
                id: 'available_amounts',
                show:false,
                minWidth:100,
                accessor:row => row.available_amounts
            },

            {
                Header : "Status",
                id: 'approval_json',
                minWidth:100,
                accessor:row =>  row.approval_json.filter((json=>json.person_id == get('user_id'))).map((json)=>{
                    return(
                      <p style={{fontSize:'13'}}>{ json.approved ? 'Pending with others' : 'Pending your Approval' }</p>
                    )
                   })   
                
                             
            },
            {
                Header : "Current FA Limit",
                id: 'crnt_fa_limit',
                minWidth:100,
                accessor:row => {return lbl_amt(row.crnt_fa_limit , this.currency_code)}
            },
            {
                Header : "Requested On",
                id: 'created_at',
                minWidth:100,
                accessor: row => {return format_date(row.created_at)}
            },
            {
                Header : "Action",
                id: 'ids', 
                Cell: ({ row }) => (<Button variant="primary" onClick={e => this.handleApprovalModal(e ,row)}>view</Button> )
            }         
        ]
    }

    handleApprovalModal = (e, event) =>{
        this.setState({approval_modal: true, modal_data : event})
    }

    handleApprovalModalClose = (event) =>{
        this.setState({approval_modal: false})
    }

    handleupdReqStatus = (event) =>{
        const action = event.target.value
        const id = event.target.id
        const amount = this.state.upgrade_amt

        if(!amount && action == 'approve'){
            alert("Please select the amount");
        }else{
            const resp = window.confirm(`Are you sure to ${action} ?`);
            if(resp == false){
            return;
            }
            updgradeReqStatus(this.req({action,id,amount}))
            .then((response) => {
                this.resp(response)
                if(!response){return}
                if(response){
                    if(response.status === "success"){
                        alert(response.message); 
                        this.setState({approval_modal: false})
                        this.drawTable()				
                    }
                }
            })
        }
    }

    handleUpgradeAmtCheckbox = (event) => {
        this.setState({upgrade_amt : event.target.value});
    }

    render(){ 
        var approved = false

        this.state.modal_data._original && this.state.modal_data._original.approval_json.filter((json=>json.person_id == get('user_id'))).map(json =>

            approved = json.approved
        )
    
        return (
        <>
        <Accordion>
                <Card>		
                <div className="card-header card-header-icon card-header-primary home_header_d-flex_at_response justify-space-between">
                    <h4 className="card-title text-white tableHead_font title-bottom ">Pending FA Upgrade Requests</h4>
                    <Accordion.Toggle className = "col" as = {Card.Header} eventKey="0" onClick = {()=>this.handleToggle("Account")}>
                            <BsChevronDown className = {this.state.show_table ? "arrow-icon down accordin-arrow" : "arrow-icon accordin-arrow"} size="18"/>
                        </Accordion.Toggle>		
                </div>
                
                <div className="card-body no-padding justify-content-center">
                <Accordion.Collapse eventKey="0">
							<Card.Body>
                    <CommonReactTable showPag={true} defaultPageSize={20} minRow={0} row_data={this.state.copy !== undefined ? this.state.copy : []} column_data={this.state.columns} resizable={false} defaultSorted={[{id:"created_at",desc:true}]}/>                                
                    </Card.Body>
						</Accordion.Collapse>
                </div>
                </Card>
            </Accordion>
        <Modal show={this.state.approval_modal} onHide={this.handleApprovalModalClose}  backdrop="static" size="xl" style={{position: "fixed"}}>
            <Modal.Header closeButton className="modal-close-btn" show={this.state.approval_modal} onHide={this.handleApprovalModalClose}>
                <Modal.Title>FA Upgrade Request</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <form>
                    <div className="floatAdvanceperiodHeaderBox">
                        <h2 className="floatAdvanceperiodHeader no-margin">Customer :<h2 className="floatAdvanceBluetitle no-margin">{this.state.modal_data.cust_id}</h2></h2>
                    </div>
                    <div className="row no-margin pastFloatAdvanceBox justify-content-center">
                        <div className="col-lg-10 no-padding personalDetailsbox ">
                            <ul>
                                <li>
                                    <div className="row no-margin justify-content-center">
                                        <div className="no-padding col-lg-5">
                                            <p className="personalDetailstext no-margin">Biz Name</p>
                                        </div>
                                        <div className="col-lg-5 no-padding">
                                            <ul className="personalPastDetailstext">
                                                <li>{this.state.modal_data.biz_name}</li>
                                            </ul>                            
                                        </div> 
                                                            
                                    </div>
                                </li>
                                <li>
                                    <div className="row no-margin justify-content-center">
                                        <div className="no-padding col-lg-5">
                                            <p className="personalDetailstext no-margin">Requested On</p>
                                        </div>
                                        <div className="col-lg-5 no-padding">
                                            <ul className="personalPastDetailstext">
                                                <li>{this.state.modal_data.created_at}</li>
                                            </ul>                            
                                        </div> 
                                    </div>
                                </li>                            
                            </ul>
                        </div>
                            
                    </div>


                    {this.state.modal_data.cust_id&&
                            <LastNFAs cust_id = {this.state.modal_data.cust_id.props.children} last_n_fas ='4' />
                        
                    }
                    {this.state.modal_data._original && this.state.modal_data._original.last_loan &&
                        <>
                            <div className="floatAdvanceperiodHeaderBox mt-2">
                                <h2 className="floatAdvanceperiodHeader no-margin">Last FA</h2>
                            </div>
                            <div className="row no-margin pastFloatAdvanceBox justify-content-center">
                                <div className="col-lg-10 no-padding personalDetailsbox ">
                                    <ul>
                                        <li>
                                            <div className="row no-margin justify-content-center">
                                                <div className="no-padding col-lg-5">
                                                    <p className="personalDetailstext no-margin">Float Advance</p>
                                                </div>
                                                <div className="col-lg-5 no-padding">
                                                    <ul className="personalPastDetailstext">
                                                        <li>{lbl_amt(this.state.modal_data._original.last_loan.loan_principal, this.currency_code)}</li>
                                                    </ul>                            
                                                </div> 
                                                                    
                                            </div>
                                        </li>
                                        <li>
                                            <div className="row no-margin justify-content-center">
                                                <div className="no-padding col-lg-5">
                                                    <p className="personalDetailstext no-margin">Disbursed Date</p>
                                                </div>
                                                <div className="col-lg-5 no-padding">
                                                    <ul className="personalPastDetailstext">
                                                        <li>{format_date(this.state.modal_data._original.last_loan.disbursal_date)}</li>
                                                    </ul>                            
                                                </div> 
                                            </div>
                                        </li>                            
                                    </ul>
                                </div>
                            </div>
                        </>

                    }

                {this.state.modal_data._original && this.state.modal_data._original.approval_json &&
                        <>
                            <div className="floatAdvanceperiodHeaderBox mt-2">
                                <h2 className="floatAdvanceperiodHeader no-margin">All Approvers</h2>
                            </div>

                            {this.state.modal_data._original.approval_json.map(json =>
                                <div className="row no-margin pastFloatAdvanceBox justify-content-center">
                                    <div className="col-lg-10 no-padding personalDetailsbox ">
                                        <ul>
                                            <li>
                                                <div className="row no-margin justify-content-center">
                                                    <div className="no-padding col-lg-5">
                                                        <p className="personalDetailstext no-margin">{dd_value(json.approver_name)}<br/>{json.person_id == get('user_id') && <>(You)</>}</p>
                                                    </div>
                                                    <div className="col-lg-5 no-padding">
                                                        <ul className="personalPastDetailstext">
                                                            <li>{json.approved ? 'Approved' : 'Pending'}</li>
                                                        </ul>                            
                                                    </div> 
                                                </div>
                                            </li>                           
                                        </ul>
                                    </div>
                                </div>
                            )}                            
                        </>

                    }
                                           <div className="row justify-content-center align-items-center ">
                        <label  className="col-sm-3 personalDetailstext no-margin no-padding"><p className= "personalDetailstext" style={{paddingTop:"20px"}}>Select Amount to upgrade</p></label>
                            {this.state.modal_data.available_amounts && 
                                this.state.modal_data.available_amounts.map((amount) =>{
                                    return(
                                        <ul className="personalPastDetailstext">
                                            <li><label><input type="radio" name="appr" value={amount} onClick={this.handleUpgradeAmtCheckbox} /> &nbsp;&nbsp;
                                                {this.state.modal_data._original.requested_amount == amount ? <><b>{lbl_amt(amount, this.currency_code)} </b>{this.state.modal_data.available_amounts.length  > 1 ? <span>(customer requested amount)</span> : <span></span>}</> : lbl_amt(amount, this.currency_code)}&nbsp;&nbsp;&nbsp;
                                                </label>
                                            </li>
                                            
                                        </ul>    
                                    )  
                                })
                            }
                        </div> 
                </form>
            </Modal.Body>
            <Modal.Footer className="row my-2 justify-content-center align-items-center py-2" >
            {this.state.modal_data._original && approved == false &&

               
            <>
                <Button id = {this.state.modal_data._original.id}  value = "approve"   variant="primary" disabled={this.state.calling_api}  onClick = {this.handleupdReqStatus} >Approve</Button>
                <div></div>
                <Button id = {this.state.modal_data._original.id}   value = "reject"  variant="danger" disabled={this.state.calling_api} style={{marginLeft:10}} onClick = {this.handleupdReqStatus} >Reject</Button>
                </>
            }
            </Modal.Footer>
        </Modal> 
    </>
        );

    }
}


export default PendingFAUpgradeRequests;
