import {call_fetch, app_path} from './../helpers/fetch_helper';


export const createTask = request => {
    return call_fetch(app_path + '/task/create', request)
}
 
export const listTaskRequests = request => {
    return call_fetch(app_path + '/task/list', request)
}
export const approveTaskReq = request => {
    return call_fetch(app_path + '/task/approval', request)
}