    componentWillMount()
    {
        this.get_report_date();
        this.generate_report();

    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.country_code != this.state.country_code ) {
            if(this.state.country_code == "*"){
                this.setState({viewBy : false})
            }
            this.generate_report();
          
        } else if (prevState.activeCurrency != this.state.activeCurrency || prevState.activeCurrencyVal != this.state.activeCurrencyVal ) {
            if (this.state.revpercust && this.state.revPerFa && this.state.revenueDiv && this.state.revpercust && this.state.revpercustCntry &&
               this.state.revPerFa && this.state.revPerFaCntry && this.state.totdisb && this.state.totdisbCntry && this.state.revenueForCountryDiv ){
        this.load_currency_charts();
                    }
        }else if (prevState.startDate != this.state.startDate || prevState.endDate != this.state.endDate){
            this.reload_charts()
        }
    }

    // componentDidMount() {
    //     React.createElement("Script",{},"$('.carousel').carousel({\n" +
