import React,{useRef,useState} from "react";
import ReactTable from "react-table-6";
import { CSVLink, CSVDownload } from "react-csv";
import moment from "moment";
import { ExcesstimeAction } from "../UI/common/partial";


const NullComponent = () => {
    return(
        <div>
            <p className="no-margin text-center" style={{paddingTop:10, color:'#656d83'}}>No data found</p>
        </div>
    )
};

export default function CommonReactTable ({row_data,column_data,showPag,minRow,resizable,defaultSorted,defaultPageSize,sortable,exportCSV,csvFile,csvData,timeServer,excessTimeaction,appliedRowblink,appliedRowblinktime, approveRowblinktime, paymentRowblinktime,csvstyle}) {                    
    const getTrProps = (state, rowInfo, instance) => {                      
        if(excessTimeaction && rowInfo){
            if(appliedRowblink){                 
                if(paymentRowblinktime){
                    const time_diff = moment.duration(moment(timeServer).diff(moment(rowInfo.original.updated_at)))                                                                                                 
                    return {
                        className: time_diff._milliseconds > (paymentRowblinktime*60000) ? 'redBlink_table' : '',                                        
                    } 
                }
                const time_diff = moment.duration(moment(timeServer).diff(moment(rowInfo.original.loan_appl_date)))                                                                                                 
                return {
                    className: time_diff._milliseconds > (appliedRowblinktime*60000) ? 'redBlink_table' : '',                                        
                }                        
            }else if(!appliedRowblink){                                
                const time_diff1 = moment.duration(moment(timeServer).diff(moment(rowInfo.original.loan_approved_date)))
                const time_diff2 = moment.duration(moment(timeServer).diff(moment(rowInfo.original.loan_appl_date)))                                                                                                                                                
                return {
                    className: ((time_diff1._milliseconds > (approveRowblinktime*60000)) || (time_diff2._milliseconds > (approveRowblinktime*60000))) ? 'redBlink_table' : '',                    
                }
            }                        
        }
        return{}           
    }    

    return (
        <div>  
            { row_data  ?
            <>
            {exportCSV &&
                <div className={csvstyle ? " export_button_acordin export_btn " : "export_btn text-right "} style={{padding:'10px !important'}} >
                    <CSVLink filename={`${csvFile}.csv`} data={csvData} className="btn btn-secondary btn-std" style={{color:'#fff',padding:'5px !important'}}>Export as CSV</CSVLink>	                                      
                </div> 
            }   
              
            <div className={`table-responsive ${row_data.length === 0 ? 'no_scroll' : ''} customer_list_reactTable border_bot`}>
                <ReactTable NoDataComponent={NullComponent} showPaginationBottom={showPag && row_data.length > 0 && true} minRows = {minRow}  data={row_data}  
                columns={column_data} resizable={resizable} sortable={sortable} defaultPageSize={defaultPageSize}  defaultSorted={defaultSorted} getTrProps={(state, rowInfo, column) => getTrProps(state, rowInfo, column)} />
            </div>
            </>   
             :
             <div class=" spinner-border loader-spin" role="status">
             <span class="sr-only">Loading...</span>
           </div>
           }
        </div>
    )
}