handleTextboxValue = (event) =>{
	if(event.target.value != "")
	{
		this.setState({search_by_dropdown_style: "disable_div", search_by_textbox_style : "show_div",section1: "disable_div", cust_div_style: "disable_div"});
	}else{
		this.setState({search_by_dropdown_style: "show_div", cust_div_style: "show_div"});
	}
}

handleCheckboxClick = (event) => {
	if(event.target.checked)
	{
		event.target.value = true;

		this.setState({submitted_field_style: "disable_div", __from: "", __to: "", today: true});
		this.handleElementReset(this.state.action_date_field + '__from');	
		this.handleElementReset(this.state.action_date_field + '__to');	

	}else{
		event.target.value = false;
		event.target.value = null;
		this.setState({submitted_field_style: "show_div", today: false});
	}
	this.handleChange(event);
}

handleBorrowerCheckboxClick = (event) =>{
	
	if(event.target.checked)
	{
		event.target.value = true;
	}
	else
	{
		event.target.value = false;
	}
	this.handleChange(event);
}


handleResetClick = (event) => {
	this.setState({section3:"show_div",search_by_textbox_style:"show_div"});
	this.handleReset();
	//this.setState({search_btn_style: ""});
	this.setState({search_by_dropdown_style: "show_div"});
	this.setState({search_by_textbox_style: "show_div"});
	this.setState({submitted_field_style: "show_div"});
	this.setState({approved_field_style: "show_div"});
	this.setState({cust_div_style: "show_div"});
	this.setState({loan_id_div_style: "show_div"});

//	console.log("search_btn_style",this.state.search_btn_style);
	console.log("search_by_dropdown_style",this.state.search_by_dropdown_style);
	console.log("search_by_textbox_style",this.state.search_by_textbox_style);
	console.log("submitted_field_style",this.state.submitted_field_style);
	console.log("approved_field_style",this.state.approved_field_style);
	console.log("cust_div_style",this.state.cust_div_style);
	console.log("loan_id_div_style",this.state.loan_id_div_style);
	
}

handleChangeReset = ()=>{
	this.setState({__from:"",__to:"",reg_date_from:"",reg_date_to:"",section1:"show_div",section3:"show_div",section2:"show_div"});
}
