import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import { Link} from 'react-router-dom';
import {getMasterData,dd_value} from '../../../actions/common_actions';
import {Button } from 'react-bootstrap';
import DatePicker from 'react-datetime';
import moment from 'moment';
import '../../../styles/flow.css';
import {getRefAccounts} from '../../../actions/account_txn_actions';
import $ from 'jquery';
import {check_priv, get,get_item} from '../../../helpers/storage_helper';
import {rt_algn,lbl_amt,Homeacc_prvdrLogoselector} from '../../../helpers/common_helper';
import {format_date_tbl} from '../../../helpers/common_helper';
import { list_lender_txn_stmts } from '../../../actions/lender_actions';
import CommonReactTable from '../../../helpers/commonReact_table';
import { AccountTransactionToolTipLabel,TooltipLabel,ExcesstimeAction } from '../../common/partial';
import {ImArrowUpRight,ImArrowDownLeft} from "react-icons/im";
import {reinitiateRecon} from '../../../actions/loan_actions';

import ReconStatus from '../component/ReconStatus';
class UnknownTransactions extends FlowComponent{


    state = {
        txn_type : "",
        from_date : "",
        to_date : "",
        recon_status : "",
        rowdatas:[],
        columndatas:[],
        copy:[]
    }

    componentWillMount ()
    {
        if(this.props.today === 'true') {
            const statement_search = {recon_status: 'recon_not_done', stmt_txn_date__from: this.props.match.params.today, stmt_txn_date__to: this.props.match.params.today}
            this.unknown_txn_status(statement_search)
        }
        else {
            const today = new Date();
            function formatDate(date, format) {
                format = format.replace('mm', date.getMonth() + 1)
                    .replace('yy', date.getFullYear())
	                .replace('dd', date.getDate());
                return format
            } 
            this.setState({from_date : `${today.getFullYear()}-01-01`});   
            this.setState({to_date : formatDate(today, "yy-mm-dd")});        
            const statement_search = {stmt_txn_type: "credit", recon_status: 'unmatched_credits', stmt_txn_date__from: this.state.from_date, stmt_txn_date__to: formatDate(today, "yy-mm-dd")}
            this.unknown_txn_status(statement_search)
            this.setState(state => state.txn_type = "credit")
        }
    }


    handleTxnType = (event) => {
      this.setState({'txn_type' : event.target.value})
    }

    handleReconStatus = (event) => {
        this.setState({'recon_status' : event.target.value});
    }

    handleDateChange = (id, value) => {
        value = moment(value).format("YYYY-MM-DD")
        if(id === "from_date"){
            this.setState({from_date : value});
        } else if(id === "to_date"){
            this.setState({to_date : value});
        }
      }

    handleClick = async () => {
        if(this.validate_search()){
            let statement_search;
            if (this.state.recon_status){
                statement_search = {stmt_txn_type: this.state.txn_type, recon_status: this.state.recon_status, stmt_txn_date__from: this.state.from_date, stmt_txn_date__to: this.state.to_date};
            } else {
                statement_search = {stmt_txn_type: this.state.txn_type, recon_status: 'unmatched_credits', stmt_txn_date__from: this.state.from_date, stmt_txn_date__to: this.state.to_date};
            }
            await list_lender_txn_stmts(this.req({statement_search}, false, 'statement_search'))
            .then((response) => {
                this.resp(response)
                if(!response)
                {
                    return null;
                }
                if(response.status === "success"){
                this.setState(state=>state.rowdatas = response.data.account_txns)
                this.setState(state=>state.copy = response.data.account_txns)
                this.setState(state=>state.columndatas=this.columndatas())
                this.setState({account_result:"show_div"});   
                }else{
                  alert(response.message + "\n" + JSON.stringify(response.data));  
                }
            });
          }    	
        } 
        

    validate_search(){
        if(this.state.txn_type && this.state.from_date && this.state.to_date){
            return true
        }else{
            alert("Please enter valid search criteria")
            return false
        }
    }

    chk_txn_type(record){
        const cr_amt = record.cr_amt
          if (cr_amt === 0) {
              return<span >{dd_value(record.stmt_txn_type)} <ImArrowUpRight className="ml-2" color="red"/></span>
          }
          else{
              return <span>{dd_value(record.stmt_txn_type)} <ImArrowDownLeft className="ml-2" color = "green"/></span>
          }
    }

    handleReinitiateRecon = (event, row)=>{
        const amount = row.stmt_txn_type == 'credit' ? row.cr_amt : row.stmt_txn_type == 'debit' && row.dr_amt
        const resp = window.confirm("Are you sure to reinitiate this recon transaction ?")
        if(resp == false){
          return;
        }

         reinitiateRecon(this.req({acc_stmt_id: row.id, txn_id : row.stmt_txn_id, amount : amount}))
        .then((response) => {
          this.resp(response)
          if(!response){return };
          if(response.status === "success"){
            alert(response.message);  
            this.handleClick()
        }
        })		
    }

     
    unknown_txn_status = async (statement_search) => {
        await list_lender_txn_stmts(this.req({statement_search}, false, 'statement_search'))
            .then((response) => {
                this.resp(response)
                if(!response)
                {
                    return null;
                }
                if(response.status === "success"){
                this.setState(state=>state.rowdatas = response.data.account_txns)
                this.setState(state=>state.copy = response.data.account_txns)
                let column = this.columndatas()
                column.splice(5, 1)
                column.splice(5, 0, {
                    Header : <div className='text-right'>Credit Amount</div>,
                    width:130,
                    maxWidth: 130,
                    accessor: 'cr_amt',
                    Cell: row => {return rt_algn(lbl_amt(row.value, this.currency_code))}
                })
                column.splice(6, 0, {
                    Header : <div className='text-right'>Debit Amount</div>,
                    width:130,
                    maxWidth: 130,
                    accessor: 'dr_amt',
                    Cell: row => {return rt_algn(lbl_amt(row.value, this.currency_code))}
                })
                this.setState(state=>state.columndatas=column)
                this.setState({account_result:"show_div"});   
                }else{
                  alert(response.message + "\n" + JSON.stringify(response.data));  
                }
            });
    }
        
    columndatas(){
        let column = [
            {
                Header : "Stmt Txn Type",
                id: "stmt Txn",
                maxWidth: 90,
                accessor:row => {return this.chk_txn_type(row)}
            },
            {
                Header : "Network",
                accessor: "network_prvdr_code",  
                sortable: false,
                maxWidth:85,               
                Cell :row => <Homeacc_prvdrLogoselector logotype={row.value}/>
            },
            {
                Header : "Account Number",
                accessor: "acc_prvdr_code",
                sortable: false,
                width:130,
                maxWidth: 130,                
                Cell: row => <div className='d-flex align-items-center'> <div><Homeacc_prvdrLogoselector logotype={row.value} style={{width:'30px',height:'20px'}} /></div>
                                <div style={{paddingLeft:'10px'}}>{row.original.acc_number}</div>
                                 </div>
            },
            // {
            //     Header : "Account Number",
            //     accessor: "acc_number",
            //     sortable: false,
            //     maxWidth: 150,
            //     width:120,
            //     Cell: row => <span style={{float:'left'}}>{row.value}</span>    
            // },
            {
                Header : "Stmt Txn Date",
                width:150,
                maxWidth: 150,
                accessor: 'stmt_txn_date',
                Cell: row => format_date_tbl(row.value, true)
            }, 
            {
                Header : "Stmt Txn ID",
                width:80,
                accessor: 'stmt_txn_id'
            },
            {
                Header : <div className='text-right'>Amount</div>,
                width:130,
                maxWidth: 130,
                accessor: (this.state.txn_type==='debit')?'dr_amt':'cr_amt',
                Cell: row => {return rt_algn(lbl_amt(row.value, this.currency_code))}
            },
            {
                Header  : <div className='text-center'>Description</div>,
                accessor: 'descr',
                Cell    : row => <div className='text-center'><TooltipLabel full_text={dd_value(row.value)} no_reduce={true} /></div>
            },
            {
                Header :<div className='text-center'>Matching FA ID</div> ,
                minWidth:140,
                maxWidth:180, 
                id: 'chk_fa_id',               
                accessor : row => <div className='text-center'>{this.chk_fa_id(row)}</div>
            },
            {
                Header : "Matching Status",
                minWidth:140,
                maxWidth: 180,                
                id : 'recon_status',
                accessor: row => <TooltipLabel full_text={dd_value(row.recon_status)} overlay={row.recon_desc} no_reduce={true} />
            },
            {
                Header : "Action",
                id: "action",
                minWidth:140,
                maxWidth: 180,          
                accessor:row => <ReconStatus show_btn = {true} data={row} onSync = {this.handleClick}/>
            },
            {
                Header : "Action",
                id: "reinitiate",
                minWidth:140,
                maxWidth: 180,                 
                show: check_priv('recon', 'reinitiate_recon') ? true : false,
                
        
                accessor: row => <> { (row.recon_status != "80_recon_done" && row.recon_status != "10_capture_payment_pending" ) || (row.recon_status != null && (row.recon_status != "10_capture_payment_pending" && row.loan_doc_id == null)) ?
                                          <input id={row.id} stmt_txn_id = {row.stmt_txn_id} row = {row} amount = {row.amount} type="button" value="Recon Again" onClick={(e)=>{this.handleReinitiateRecon(e, row)}} className={`btn btn-primary btn-std}`}/>
                                          :
                                          <input disabled = {true} id={row.id}  type="button" value="Recon Again" onClick={this.handleReinitiateRecon} className={`btn btn-primary btn-std}`}/>
                                    }
        
                                  </>
              },
        ]
        return column
       
    }

    changeHandler = (e) => {		
        const toFilter = e
        const filterfrom = this.state.rowdatas
        const filteredData = filterfrom.filter(value => {
        return(
            value.stmt_txn_type !==null && value.stmt_txn_type.toLowerCase().includes(toFilter.toLowerCase()) ||
            value.stmt_txn_id !==null && value.stmt_txn_id.includes(toFilter) ||
            value.loan_doc_id !==null && value.loan_doc_id.toLowerCase().includes(toFilter.toLowerCase()) ||
            value.descr !==null && value.descr.toLowerCase().includes(toFilter.toLowerCase())         				
        )
        })
        this.setState(state=>state.copy = filteredData)		
    }


    chk_fa_id(record){

        const loan_doc_id = record.loan_doc_id
        const recon_status = record.recon_status

        if(loan_doc_id){
            return <Link to={"/fa/view/"+loan_doc_id} style = {{color:"grey"}}  target="_blank" >{loan_doc_id}</Link>
        }
        else{
            return "NA"
        } 
    }

    render(){
        return(
              
            <div className='container containerTopmargin max-width no-padding'>
                <h2 className='text-white text-center'>Unknown Transactions</h2>
                <div className="date-range" style={{padding:15}}>          
                <div className = "form-row">
                    <div className = {`form-group col-md-3`}>
                        <label>Statement Transaction Type   </label><font className="text-danger">   *  </font>
                        <select id="stmt_txn_type" className = "form-control" value = {this.state.txn_type} onChange={(e) => {this.handleTxnType(e);}} type="text" required="required">
                            {this.getSelectOptions("stmt_txn_type")}
                        </select>
                    </div>
                    <div className = {`form-group col-md-3`}>
                        <label>Start Date   </label><font className="text-danger">   *  </font>
                        <DatePicker defaultValue = {this.props.today === 'true' ? moment(this.props.match.params.today,  "YYYY-MM-DD") : moment(this.state.from_date,  "YYYY-MM-DD")} closeOnSelect={true}  onChange={(value) => {this.handleDateChange("from_date", value)}}  dateFormat="DD MMM YYYY" timeFormat={false} required="required"/>

                    </div>
                    <div className = {`form-group col-md-3`}>
                        <label>End Date   </label><font className="text-danger">   *  </font>
                        <DatePicker defaultValue = {this.props.today === 'true' ? moment(this.props.match.params.today,  "YYYY-MM-DD") : moment(this.state.to_date,  "YYYY-MM-DD")} closeOnSelect={true} onChange={(value) => {this.handleDateChange("to_date", value)}}  dateFormat="DD MMM YYYY" timeFormat={false} required="required"/>
                    </div>
                    <div className = {`form-group col-md-3`}>
                        <label>Matching Status   </label>
                        <select id="recon_status" className = "form-control" value = {this.state.recon_status} onChange={(e) => {this.handleReconStatus(e);}} type="text" required="required" >
                            {this.getSelectOptions("recon_status").filter((e) => {
                                if (e.props.value != "80_recon_done"){
                                    return e
                                }
                            })}
                        </select>
                    </div>
                </div>
                </div>
                <div className="form-row justify-content-md-center mt-3">
                    <Button className="btn btn-secondory " onClick = {this.handleClick} >Search</Button> &nbsp;
                    {/*<Button type="reset" onClick={this.handleResetClick}>Reset</Button>*/}
                </div>
                
              {(this.state.rowdatas.length !== 0) && 
              <>
                <div className='d-flex justify-content-between align-items-center' style={{padding:5}}>
                    <p className='no-margin text-white'><b>Total Entries : {this.state.copy.length}</b></p>
                    <div style={{position:'relative'}}>
                        <i class="fa-solid fa-magnifying-glass search_pos"></i>
                        <input placeholder='Search here' className='search_posInput' onChange={(e)=>this.changeHandler(e.target.value)}></input>
                        {/* <button className='btn-std btn btn-primary' onClick={()=>this.resetHandler()}>Reset</button> */}
                      </div>
                </div>
                    <CommonReactTable showPag={true} minRow={0} row_data={this.state.copy !== undefined ? this.state.copy : []} column_data={this.state.columndatas} resizable={false}  exportCSV={true} csvFile={"unknown_txns"} csvData={this.state.copy ? this.state.copy : []}/>                                         
              </>
              }
        </div> 
        )
    }
  
}
export default UnknownTransactions;