  server_time(time_zone) {
      return touch({country_code: this.country_code, time_zone: time_zone})
          .then((response) => {
                  if (!response) {
                      return false
                  }
                  ;

                  if (response.status === "success") {
                      let date = new Date(response.server_time_db)
                      console.log('date;',date)
                      this.setState({date: date})
                  }
              }
          );
  }


