import React from "react"

export const GlobalFilter = ({filter,setFilter}) => {
    return(
        <span style={{color:'white',float:'right'}}>
            Search:{" "}
            <input  style={{margin: "0px 10px 10px 0px"}}value={filter || ""} onChange={(e)=>setFilter(e.target.value)}/>
        </span>
    )
}