import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import { Modal, Button } from 'react-bootstrap';
import { set, set_logout } from '../../../helpers/storage_helper';

class DisplayImage extends FlowComponent {
	state = { 
		modalBox: false,
		img_height:'',
		img_width:'100'
	 };
	handleImage = (event) => {
		
		this.setState({modalBox: true});

	}
	handleClose = (event) => {
		this.setState({modalBox: false});
	}	

	render(){		
		console.log("this.root-"+this.root);
		console.log("this.props.image_path-"+this.props.image_path);
		console.log("this.props.file_name-"+this.props.file_name);
		const onImgLoad = ({ target: img }) => {
			const { offsetHeight, offsetWidth } = img;
			console.log(offsetHeight, offsetWidth);
			const ratio = offsetWidth/offsetHeight
			const height = 100/ratio
			this.setState(state=>state.img_height = height)						
		  };
		
		return(
			<>
				{this.props.title &&
					<p className='text-white no-margin'>{this.props.title}</p>
				}				
				<>
					<div style={this.props.style ? this.props.style : {height:this.state.img_height,width:this.state.imgWidth,margin:'auto'} } className="text-center align-items-center">
						{this.props.file_name ?
							<a href="#">
							<img style={this.props.style ? this.props.style : {height:this.state.img_height,width:this.state.imgWidth}} src={this.root + this.props.image_path + "/s_" + this.props.file_name}
									onLoad={onImgLoad} alt={"/s_" + this.props.file_name} onClick={(e) => this.handleImage(e)}/>
							</a>

						: <p className='no-margin viewCus_labelVal'>Not uploaded</p>}
						{/* {this.props.file_name ? 
							<img src={this.root + this.props.image_path + "/l_" + this.props.file_name} 
										alt={"/l_" + this.props.file_name} onClick={(e) => this.handleImage} />
									
						: <>Not uploaded</>} */}
					</div>
				</>
				<Modal show={this.state.modalBox} onHide={this.handleClose} className="model_window">
					<Modal.Header closeButton className="modal-close-btn" show={this.state.modalBox} onHide={this.handleClose}>
						<Modal.Title>{this.props.title}</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<img src={this.root + this.props.image_path + "/l_" + this.props.file_name}
									alt={"/l_" + this.props.file_name} />
					</Modal.Body>
					<Modal.Footer>
						{/* <Button variant="secondary" onClick={this.handleClose}>Close</Button> */}
					</Modal.Footer>
          		</Modal>
			</>
			);
	}
}

export default DisplayImage;
