import ReactApexChart from 'react-apexcharts';
import FlowComponent from "../../common/component/core/FlowComponent";
import React from 'react';
import moment from "moment";

class ReportChart extends React.Component {
    constructor(props) {
        super(props);
        this.updateChartData();
    }

    updateChartData = () =>{
        const curr = this.props.curr === undefined ? '' : this.props.curr
        this.state = {

            series:
            this.props.data,
            options: {
                chart: {
                    width :'100%',
                    height: '80%',
                    zoom: {
                        enabled: true
                    }
                },
                colors: ["#00e396", " #ff3154", "#fadd17"],
                plotOptions: {
                    bar: {
                        dataLabels: {
                            position: 'top',
                        },
                    }
                },
                dataLabels: {
                    enabled: this.props.dataLabel,
                    offsetY: this.props.type === "bar" ? -20 : 0,
                    style: {
                        colors: ["#00e396", " #ff3154", "#fadd17"],
                    },
                },
                stroke: {
                    curve: 'smooth',
                    width:1
                },
                title: {
                    text: this.props.title,
                    align: 'left',
                    style:{
                        color : "#999"
                    }
                },
                legend:{
                    height : '100',
                    showForSingleSeries: true
                },
                xaxis: {
                    type: this.props.xaxistype,
                    tickPlacement : "on",
                    lines:{
                        show: true
                    },
                    labels:{
                        style:{
                            colors : '#999'
                        },
                        formatter: function(val) {
                            return moment(val).format("MMM Y");
                        }
                    }
                },
                yaxis: {
                    lines:{
                        show: true
                    },
                    labels:{
                        style:{
                            colors : '#999'
                        },
                        formatter: function(val) {
                            return (new Intl.NumberFormat("en-US")).format(val) +" "+ curr;
                        }
                    }
                }
            },


        };
    }


    render() {
        this.updateChartData();
        return (

            <div id="chart">
                <ReactApexChart options={this.state.options} series={this.state.series} type = {this.props.type} height={600} />
            </div>

        )
    }
}

export default ReportChart;